
.alert {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  text-align: center;
  z-index: 999;
}
.alert.alert-show {
    -webkit-animation: show 2s;
            animation: show 2s;
}
@-webkit-keyframes show {
0% {
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
}
20% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
}
50% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
}
80% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
}
100% {
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
}
}
@keyframes show {
0% {
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
}
20% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
}
50% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
}
80% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
}
100% {
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
}
}



